	import uniCountdown from "@/components/uni-countdown/uni-countdown.vue"

	export default {
		data() {
			return {
				orderNumber :'',//订单编号
				entrustedUnit :'',//委托单位
				inspecPerson : '',//送检人员
				orderTime : '', //下单时间
				totalprice : 0 , //总金额
				timer:''
			}
		},
		onLoad() {			
		},
		onShow() {
			uni.getStorage({
				key: 'data',
				success: (result) => {
					this.orderNumber = result.data.orderNumber
					this.entrustedUnit = result.data.entrustedUnit 
					this.inspecPerson = result.data.inspecPerson
					this.totalprice = result.data.totalprice
					this.orderTime = result.data.orderTime
				},
			})
			
			var hour = 0, minute = 59, second = 59; /*时 分 秒*/
			var millisecond = 0; //毫秒
			var timers = setInterval(() => {
				millisecond = millisecond + 50;
				if (millisecond >= 1000) {
					millisecond = 0;
					second = second - 1;
				}
				if (second <= 0) {
					second = 59;
					minute = minute - 1;
				}
                 if (minute < 0) {
					  clearInterval(timers)
					  minute = 0
					  second = 0
                      //this.back()
				 }
				this.timer = minute+'分'+second+'秒';
			}, 50);
		},
		methods: {
			back() {
				clearInterval(this.timer)
